
.text-danger {
  color: #dc3232;
}
#dokan-upgrade-notice {
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-position: bottom right;
  transition: all 0.5s;
}
#dokan-upgrade-notice:after {
  content: "";
  display: table;
  clear: both;
}
#dokan-upgrade-notice * {
  box-sizing: border-box;
}
#dokan-upgrade-notice-icon {
  width: 65px;
  float: left;
  padding: 12px 12px 12px 0;
}
#dokan-upgrade-notice-icon img {
  width: 100%;
}
#dokan-upgrade-notice-message {
  float: left;
  width: calc(100% - 65px);
}
#dokan-upgrade-notice-title {
  padding-top: 12px;
  font-size: 15px;
  font-weight: 600;
}
#dokan-upgrade-notice-buttons {
  padding-bottom: 12px;
}
